.player-item{
  width: calc(100%/3);
  // width: 100%;
  margin: 0 30px;
  position: relative;
  height: 200px;
  // background: linear-gradient(180deg,#000000  20%, #FE0000 80%);
  background-image: url('../../../../images/players/4006.jpg');
  background-position: center;
  background-size: cover;
  transition: .5s;
  cursor: pointer;
  
  @include media(small){
        margin: 0 50px;
      }

      @media (min-width: 730px) {
        margin: 0 60px;
      }

      // @include media(medium){
      //   margin: 0 80px;
      // }

  @include media(large){
        margin: 0 40px;
      }

  @include media(xlarge){
        margin: 0 45px;
      }

  @include media(xxlarge){
        margin: 0 35px;
      }

  // display: flex;
  // margin-bottom: 20px;
  &__image-container{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 185px;

    // @media (min-width: 930px) {
    //   width: 50%;
    // }

    @include media(small) {
      width: 180px;
    }

    @media (min-width: 730px) {
        width: 195px;
      }

    @include media(xlarge) {
      width: 210px;
    }

    // @media (min-width: 1100px) {
    //   width: 44%;
    // }

    // max-width: 220px;
    // max-height: 360px;
    &--team{
      width: 45%;
      right: 10px;
    }
  }
  &__image{
    width: 100%;
    // max-width: 220px;
    // max-height: 360px;
    // height: 100%;
  }
  &__info{
    width: 40%;
    display: flex;
    // flex-direction: column;
    flex-wrap: wrap;
    color: white;
    padding-left: 5px;
    // line-height: 3rem;
    &--team{
      // flex-wrap: nowrap;
      width: 52%;
      padding-left: 10px;
      height: 100%;
    }
  }
  &__number{
    flex-basis: 100%;
    font-size: 6rem;

    

    // color: $dark-main-color;
    &--team{
      // flex-basis: 20%;
      // display: flex;
      // justify-content: center;
      // align-items: center;
      color: rgba(255, 255, 255, 0.4);
      line-height: 20rem;
      font-weight: bold;
      position: absolute;
      top: 0;
      left: 0;
      // z-index: -1;
      overflow: hidden;
      font-size: 22rem;
      padding-left: 21px;
    }
  }
  &__first-name{
    flex-basis: 50%;
    font-size: 2rem;
    letter-spacing: 1.5px;
    
    @media (min-width: 730px) {
      font-size: 3rem;
    }

    @include media(large){
         font-size: 2.5rem;
      }

    &--team{
      font-size: 3rem;
      display: flex;
      align-items: flex-end;
      padding-bottom: 10px;
      flex-basis: 100%;
      justify-content: center;
    }
  }
  &__last-name{
    flex-basis: 50%;
    font-size: 2rem;
    letter-spacing: 1.5px;

     @media (min-width: 730px) {
      font-size: 3rem;
    }

    @include media(large){
         font-size: 2.5rem;
      }

    &--team{
      font-size: 3rem;
      flex-basis: 100%;
    }
  }
  &__position{
    font-size: 1.3rem;
    padding-top: 20px;

    // @include media(large){
    //      padding-top: 20px;
    //   }
 @media (min-width: 730px) {
      font-size: 1.5rem;
      padding-top: 13px;
    }
  }

  &--team{
    width: 100%;
    margin: 0;
    // display: flex;
    // align-items: center;
    
    min-width: 200px;
    position: relative;
  }

  &__name-wrapper{
    flex-basis: 100%;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    // justify-content: center;
    // align-items: center;
    // text-align: center;
    // padding-left: 20px;
    // font-size: 3rem;
  }

&:hover{
  transform: scale(1.1);
}

&--active{
  transform: scale(1.2) !important;
}

&-link{
  display: block;
  width: 43.333333%;
  text-decoration: none;
  // margin-bottom: 170px;
  margin: 0 30px 170px 30px;
  &:nth-last-child(1),&:nth-last-child(2){
      margin-bottom: 0;
    }
}

}


